import React, { Component } from 'react';

import Tooltip from 'rc-tooltip';
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';
import 'rc-tooltip/assets/bootstrap.css';

const createSliderWithTooltip = Slider.createSliderWithTooltip;
const Range = createSliderWithTooltip(Slider.Range);
const Handle = Slider.Handle;

const handle = (props) => {
  const { value, dragging, index, ...restProps } = props;
  return (
    <Tooltip
      prefixCls="rc-slider-tooltip"
      overlay={value}
      visible={dragging}
      placement="top"
      key={index}
    >
      <Handle value={value} {...restProps} />
    </Tooltip>
  );
};

export default class PaintingFilter extends Component {
	constructor() {
		super();
		this.state = {		
			filterValue : [],
			selectedTab : '',
			filterMobileShow : false,
			filterMobileCount : 1,
			filterScroll : 72,
		}

		this.handleScroll=this.handleScroll.bind(this);
	}
	componentDidMount() {
		window.addEventListener('wheel', this.handleScroll);
	}
	handleScroll(e) {
		if ( this.state.filterScroll < 73 && this.state.filterScroll > -151 ) {
			let newValue = this.state.filterScroll - e.deltaY;
			if ( newValue > 72 ){
				this.setState({ filterScroll : 72 })
			}
			else if ( newValue < -150 ) {
				this.setState({ filterScroll : -150 })
			}
			else {
				this.setState({ filterScroll : this.state.filterScroll - e.deltaY})
			}
		}
	}
 	render() {
		return(
			<div className="filter-container w-100" style={{ top : this.state.filterScroll + 'px' }}>
				<div className="container custom-container pt-5 mt-4">
				    <div className="row">
						<div className="filter col-lg-2 custom pr-4">

							<div className={`position-fixed d-block d-lg-none transition ${this.state.filterMobileShow ? 'h-100':'h-0'}`} style={{ backgroundColor: 'rgba(0,0,0,0.3)', bottom: '0',bottom: '0', left: '0', right: '0' }}>
								<div className={`position-absolute h-100 w-100 ${this.state.filterMobileShow ? 'opacity-1':'opacity-0'}`} onClick={() => this.setState({ filterMobileShow: false})}></div>
								<div className="bg-white position-fixed transition shadow-card" style={this.state.filterMobileShow ? { boxShadow: '0 -10px 25px 0 rgba(0,0,0,0.1)', bottom: '0', left: '0', right: '0', height: '80%'} : { boxShadow: '0 -10px 25px 0 rgba(0,0,0,0.1)', bottom: '0', left: '0', right: '0', height: '0'}}>
									<div className="px-3 py-3 borderbottom-dark d-flex justify-content-between align-items-center">
										<div className="font-secondary font-size-xlarge font-weight-bold">Filter</div>
										<div className="text-blue text-uppercase" onClick={() => this.setState({ filterMobileShow: false})}>Clear All</div>
									</div>
									<div className="d-flex h-100">
										<div className="bg-light h-100">
											<div className={`py-3 px-3 text-center borderbottom-dark ${this.state.filterMobileCount==1 ? 'bg-white' : 'borderright-dark'}`} onClick={() => this.setState({ filterMobileCount: 1 })}>Painting Type</div>
											<div className={`py-3 px-3 text-center borderbottom-dark ${this.state.filterMobileCount==2 ? 'bg-white' : 'borderright-dark'}`} onClick={() => this.setState({ filterMobileCount: 2 })}>Price</div>
											<div className={`py-3 px-3 text-center borderbottom-dark ${this.state.filterMobileCount==3 ? 'bg-white' : 'borderright-dark'}`} onClick={() => this.setState({ filterMobileCount: 3 })}>Height</div>
											<div className={`py-3 px-3 text-center borderbottom-dark ${this.state.filterMobileCount==4 ? 'bg-white' : 'borderright-dark'}`} onClick={() => this.setState({ filterMobileCount: 4 })}>Width</div>
											<div className="borderright-dark w-100 h-100" style={{ flex : '1' }}></div>
										</div>
										<div className="bg-white" style={{ flex : '1', overflowY: 'scroll'}}>
											{
												this.state.filterMobileCount == 1 ?
												<div className="p-3">
													<label className="checkbox-container">
														Abstract
													  	<input type="checkbox" />
													  	<span className="checkmark"></span>
													</label>
													<label className="checkbox-container">
														Figurative
													  	<input type="checkbox" />
													  	<span className="checkmark"></span>
													</label>
													<label className="checkbox-container">
														Landscape
													  	<input type="checkbox" />
													  	<span className="checkmark"></span>
													</label>
													<label className="checkbox-container">
														Canvas
													  	<input type="checkbox" />
													  	<span className="checkmark"></span>
													</label>
													<label className="checkbox-container">
														Water Color
													  	<input type="checkbox" />
													  	<span className="checkmark"></span>
													</label>
													<label className="checkbox-container">
														Oil Painting
													  	<input type="checkbox" />
													  	<span className="checkmark"></span>
													</label>
												</div> : null
											}
										</div>
									</div>
								</div>
							</div>

							<div className="position-fixed d-block d-lg-none" style={{ bottom: '0', left: '0', right: '0', zIndex: '1' }}>
								{
									this.state.filterMobileShow ? 
									<div className="d-flex align-items-center bg-dark bordertop-light">
										<div className="bg-light py-3 text-black d-flex align-items-center justify-content-center" style={{ flex : '1' }} onClick={() => this.setState({ filterMobileShow: false})}>Cancel</div>
										<div className="bg-dark py-3 text-white d-flex align-items-center justify-content-center" style={{ flex : '1' }} onClick={() => this.setState({ filterMobileShow: false})}>Apply</div>
									</div> : 
									<div className={`d-flex align-items-center bg-dark py-2 px-2`}>
										<input
											className="form-control mr-2"
											style={{ flex : '1' , height : '35px' }}
											type="Search"
											placeholder="Search"
											name="search-filter" />
										<div className="filter cursor-pointer py-2 px-4 text-white text-uppercase d-flex align-items-center">
											<i className="ion ion-ios-funnel mr-2" />
											<div onClick={() => this.setState({ filterMobileShow: true})}>Filter</div>
										</div>
									</div>
								}
							</div>
							<div className="d-none d-lg-block">
								<div className="position-relative">
									<i 
										style={{ top : '11px', left: '8px', fontSize : '14px' }}
										className="ion ion-md-search position-absolute text-light" />
									<input
										className="form-control"
										style={{ height : '35px', paddingLeft : '2rem' }}
										type="Search"
										placeholder="Search"
										name="search-filter" />
								</div>

								<div className="pt-5">
									<p className="font-weight-bold text-uppercase">Painting Type</p>
									<label className="checkbox-container">
										Abstract
									  	<input type="checkbox" />
									  	<span className="checkmark"></span>
									</label>
									<label className="checkbox-container">
										Figurative
									  	<input type="checkbox" />
									  	<span className="checkmark"></span>
									</label>
									<label className="checkbox-container">
										Landscape
									  	<input type="checkbox" />
									  	<span className="checkmark"></span>
									</label>
									<label className="checkbox-container">
										Canvas
									  	<input type="checkbox" />
									  	<span className="checkmark"></span>
									</label>
									<label className="checkbox-container">
										Water Color
									  	<input type="checkbox" />
									  	<span className="checkmark"></span>
									</label>
									<label className="checkbox-container">
										Oil Painting
									  	<input type="checkbox" />
									  	<span className="checkmark"></span>
									</label>
								</div>
								
								<div className="pt-5">
									<p className="font-weight-bold text-uppercase">Price</p>
								</div>
								<div className="pr-5">
									<Range min={0} max={20} defaultValue={[0, 100]} tipFormatter={value => `${value}%`} />
									<p className="mt-2">$50 – 50,000+</p>
								</div>
								<div className="pt-5">
									<p className="font-weight-bold text-uppercase">Height</p>
								</div>
								<div className="pr-5">
									<Range min={0} max={20} defaultValue={[0, 100]} tipFormatter={value => `${value}%`} />
									<p className="mt-2">10 - 120+ inch</p>
								</div>
								<div className="pt-5">
									<p className="font-weight-bold text-uppercase">Width</p>
								</div>
								<div className="pr-5">
									<Range min={0} max={20} defaultValue={[0, 100]} tipFormatter={value => `${value}%`} />
									<p className="mt-2">10 - 120+ inch</p>
								</div>
								<div className="pt-5">
									<p className="font-weight-bold text-uppercase">Colors</p>
								</div>
								<div className="d-flex flex-wrap">
									<label className="checkbox-color mr-3 mb-3">
										<input type="checkbox" />
										<div className="color" style={{ backgroundColor: '#8EC25A'}}></div>
									</label>
									<label className="checkbox-color mr-3 mb-3">
										<input type="checkbox" />
										<div className="color" style={{ backgroundColor: '#4FA2C8'}}></div>
									</label>
									<label className="checkbox-color mr-3 mb-3">
										<input type="checkbox" />
										<div className="color" style={{ backgroundColor: '#C3A750'}}></div>
									</label>
									<label className="checkbox-color mr-3 mb-3">
										<input type="checkbox" />
										<div className="color" style={{ backgroundColor: '#FFC8A9'}}></div>
									</label>
									<label className="checkbox-color mr-3 mb-3">
										<input type="checkbox" />
										<div className="color" style={{ backgroundColor: '#C31019'}}></div>
									</label>
									<label className="checkbox-color mr-3 mb-3">
										<input type="checkbox" />
										<div className="color" style={{ backgroundColor: '#60AD87'}}></div>
									</label>
									<label className="checkbox-color mr-3 mb-3">
										<input type="checkbox" />
										<div className="color" style={{ backgroundColor: '#FE5727'}}></div>
									</label>
									<label className="checkbox-color mr-3 mb-3">
										<input type="checkbox" />
										<div className="color" style={{ backgroundColor: '#C8C7B6'}}></div>
									</label>
									<label className="checkbox-color mr-3 mb-3">
										<input type="checkbox" />
										<div className="color" style={{ backgroundColor: '#FFFFFF'}}></div>
									</label>
									<label className="checkbox-color mr-3 mb-3">
										<input type="checkbox" />
										<div className="color" style={{ backgroundColor: '#687488'}}></div>
									</label>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}


