import React, { Component } from 'react';

import { NavLink } from 'react-router-dom';


export default class Navbar extends Component {
	render() {
		return(
			<div className="custum-nav d-flex justify-content-center bg-white shadow-card borderbottom-dark fixed-top w-100 py-2">
				<nav className="navbar navbar-light navbar-expand-lg px-3 w-100 w-md-auto px-md-0 custom-container">
				  <NavLink
				  	to="/" exact activeClassName="navbar-brand pl-3 mr-4"
				  	className="navbar-brand pl-3 mr-4"
				  	style={{ fontSize: '1.2rem', letterSpacing: '1px', fontWeight: '700'}}>
				  	{/*GLAZE*/}
				  	<img src={require('../assets/images/logo39.svg')} className="mb-0" height={30}/>
				  </NavLink>

				  <button className="navbar-toggler collapsed border-none" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
				    <div className="menu">
				    	<div className="line"></div>
				    	<div className="line"></div>
				    	<div className="line"></div>
				    </div>
				  </button>

					<div className="d-lg-none" style={{ flexBasis: '100%'}}>
					  <div className="collapse navbar-collapse" id="navbarSupportedContent">
					  	<div className="py-5 px-3 font-weight-bold d-flex flex-column align-items-start">
					  		<NavLink to="/" activeClassName="py-3 text-black" className="py-3 text-black" data-toggle="collapse" data-target="#navbarSupportedContent">Paintings</NavLink>
					  		<NavLink to="/help" activeClassName="py-3 text-black" className="py-3 text-black" data-toggle="collapse" data-target="#navbarSupportedContent">Need Help?</NavLink>
					  		<NavLink to="/upload" activeClassName="py-3 text-black" className="py-3 text-black" data-toggle="collapse" data-target="#navbarSupportedContent">Upload Your Artwork</NavLink>
					  		{
					  			this.props.authorise ?
					  			<div className="d-flex flex-column">
						  			<NavLink to="/" activeClassName="py-3 text-black" className="py-3 text-black" data-toggle="collapse" data-target="#navbarSupportedContent"><i className="ion ion-md-person mr-3" />My Profile</NavLink>
						  			<NavLink to="/" activeClassName="py-3 text-black" className="py-3 text-black" data-toggle="collapse" data-target="#navbarSupportedContent"><i className="ion ion-md-paper mr-3" />Edit Profile</NavLink>
						  			<NavLink to="/" activeClassName="py-3 text-black" className="py-3 text-black" data-toggle="collapse" data-target="#navbarSupportedContent"><i className="ion ion-md-log-out mr-3" />Logout</NavLink>
						  		</div> :
						  		<div className="d-flex flex-column">
							  		<div className="py-3" data-toggle="modal" data-target="#login">Login</div>
						  			<div className="py-3" data-toggle="modal" data-target="#login">SignUp</div>
						  		</div>
					  		}
					  	</div>
					  </div>
					</div>

				  <div className="collapse navbar-collapse">
				    <ul className="navbar-nav mr-auto">
				      <li className="nav-item ml-3 dropdown">
				        <NavLink to="/" exact activeClassName="nav-link py-4 px-2 active" className="nav-link py-4 px-2">
				          Paintings
				        </NavLink>
				        <div className="dropdown-menu shadow-light border-dark">
				          <NavLink to="/" activeClassName="dropdown-item py-2" className="dropdown-item py-2">Popular Paintings</NavLink>
				          <NavLink to="/" activeClassName="dropdown-item py-2" className="dropdown-item py-2">Trending Paintings</NavLink>
				          <NavLink to="/" activeClassName="dropdown-item py-2" className="dropdown-item py-2">Paintings from Followings</NavLink>
				        </div>
				      </li>
				      <li className="nav-item ml-3">
				        <NavLink to="/help" exact activeClassName="nav-link py-4 px-2 active" className="nav-link py-4 px-2">
				          Need Help?
				        </NavLink>
				      </li>
				      {/*<li className="nav-item ml-3 dropdown">
				        <a className="nav-link py-4 px-2">
				          Artists
				        </a>
				        <div className="dropdown-menu shadow-light border-dark">
				          <a className="dropdown-item py-2">Popular Artist</a>
				          <a className="dropdown-item py-2">Trending Artist</a>
				          <div className="dropdown-divider"></div>
				          <a className="dropdown-item py-2">Search For Artist</a>
				        </div>
				      </li>*/}
				      	<li className="nav-item ml-3">
					        <NavLink to="/upload" activeClassName="nav-link py-4 px-2 active" className="nav-link py-4 px-3">Upload Your Artwork</NavLink>
					    </li>
				    </ul>
				    {
				    	this.props.authorise ?
				    	<div className="my-2 my-lg-0">
					    	<ul className="navbar-nav mr-auto">
					    		<li className="nav-item ml-3 dropdown">
							        <a className="nav-link py-4 px-3">
							          <i className="ion ion-md-notifications mx-2 font-size-xlarge"></i>
							        </a>
							        <div className="dropdown-menu shadow-light border-dark" style={{ width : '180px' }}>
							          <a className="dropdown-item py-2 text-light borderbottom-light text-wrap">You Currently dont have any Notification</a>
							        </div>
							    </li>
							    <li className="nav-item ml-3 dropdown">
							        <a className="nav-link py-4 px-3">
							          Proksh Luthra <i className="ion ion-ios-arrow-down ml-2"></i>
							        </a>
							        <div className="dropdown-menu shadow-light border-dark">
							          <NavLink to="/profile" activeClassName="dropdown-item py-2 bg-dark text-white" className="dropdown-item py-2"><i className="ion ion-md-person mr-3" />My Profile</NavLink>
							          <a className="dropdown-item py-2"><i className="ion ion-md-create mr-3" />Edit Profile</a>
							          <div className="dropdown-divider"></div>
							          <a className="dropdown-item py-2" onClick={this.props.unauthorise}><i className="ion ion-md-log-out mr-3" />Log Out</a>
							        </div>
							    </li>
							  </ul>
					    </div> :
				    	<div className="form-inline my-2 my-lg-0">
					      <a className="nav-link py-4 cursor-pointer text-black" style={{ paddingLeft : '2.5rem', paddingRight : '2.5rem' }} data-toggle="modal" data-target="#login">Log in</a>
					      <button className="btn bg-dark text-white py-3 font-weight-bold" style={{ paddingLeft : '2.5rem', paddingRight : '2.5rem' }} data-toggle="modal" data-target="#signup">Sign Up</button>
					    </div>
				    }
				  </div>
				</nav>
			</div>
		);
	}
}


