import React, { Component } from 'react';
import './assets/css/style.css';

import { BrowserRouter, Route, Switch } from 'react-router-dom';

import Navbar from './components/Navbar.js';
import LoginModal from './components/LoginModal.js';
import SignupModal from './components/SignupModal.js';

import PaintingsSearch from './pages/PaintingsSearch.js';
import UploadPage from './pages/UploadPage.js';
import BuyPage from './pages/BuyPage.js';
import ProfilePage from './pages/ProfilePage.js';
import OthersProfilePage from './pages/OthersProfilePage.js';
import ErrorPage from './pages/ErrorPage.js';
import Help from './pages/Help.js';





class App extends Component {
	constructor() {
		super();
		this.state = {
			authorise : false,
		}
	}
	render() {
	    return (
	      <BrowserRouter>
	      	<div>
		      	<Navbar authorise={this.state.authorise} unauthorise={() => this.setState({ authorise : false })} />
		      	<LoginModal authorise={this.state.authorise} authorise={() => this.setState({ authorise : true })}/>
		      	<SignupModal authorise={this.state.authorise} authorise={() => this.setState({ authorise : true })}/>	
		      	<Switch>
			      	<Route path="/" component={ PaintingsSearch } exact />
			      	<Route path="/buy" component={ BuyPage } />
			      	<Route path="/upload" component={ UploadPage } />
			      	<Route path="/help" component={ Help } />
			      	<Route path="/profile" component={ ProfilePage } />
			      	<Route path="/other-profile" component={ OthersProfilePage } />
			      	<Route component={ErrorPage} />
			     </Switch>
			</div>
	      </BrowserRouter>
	    );
	}
}

export default App;



