import React, { Component } from 'react';



export default class Help extends Component {
	render() {
		return(
			<div>

		        <div className="main-body mt-5 pt-5">

		          <div className="container custom-container pt-5 mt-4">
		            <div className="row">
		            	<div className="col-12">
		            		<p className="font-secondary" style={{ fontSize : '2rem'}}>Hola, Need Help ?</p>
		            		<p>We can Help you with Anything from Selecting any painting to offer you Costomisable Painting or just General Issues. Write us below and we will Get back to You.</p>
		            		<textarea className="form-control w-100 mb-3" style={{ height : '15rem' }} />
		            		<button className="btn bg-dark text-white px-4 py-3">Send</button>
		            	</div>
		            </div>
		          </div>

		        </div>
		    </div>
		);
	}
}


