import React, { Component } from 'react';

export default class LoginModal extends Component {
	render() {
		return(
				<div className="modal fade" id="login" tabIndex="-1" role="dialog" aria-labelledby="login" aria-hidden="true">
				  <div className="modal-dialog" role="document">
				    <div className="modal-content bg-light border-dark shadow-dark">
				      <div className="modal-header font-secondary p-4">
				        <h5 className="modal-title" id="login">Login</h5>
				        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
				          <span aria-hidden="true">&times;</span>
				        </button>
				      </div>
				      <div className="modal-body bg-white">
				      	<div className="p-5">
					        <button className="btn text-white px-4 py-4 w-100 font-weight-bold position-relative mb-3" style={{ backgroundColor : '#3B5998' }} data-dismiss="modal" onClick={this.props.authorise}>
					        	<i className="ion ion-logo-facebook position-absolute font-size-xlarge translateY-50" style={{ left : '1.5rem', top : '50%' }} />
					        	Log in with Facebook
					        </button>
					        <button className="btn px-4 py-4 w-100 font-weight-bold position-relative mb-3" style={{ backgroundColor: 'transparent', color: '#EA4335', border: '1px solid #EA4335' }} data-dismiss="modal" onClick={this.props.authorise}>
					        	<i className="ion ion-logo-googleplus position-absolute font-size-xlarge translateY-50" style={{ left : '1.5rem', top : '50%' }} />
					        	Log in with Google
					        </button>
					        <div className="w-100 p-relative d-flex align-items-center justify-content-center font-weight-bold py-4">OR</div>
					        <div className="w-100">
					        	<input className="form-control w-100 mb-3" placeholder="Email Id" style={{ height : '40px'}} />
					        	<input className="form-control w-100 mb-3" placeholder="Password" style={{ height : '40px'}} />
					        	<button className="btn bg-dark text-white font-weight-bold py-4 w-100" data-dismiss="modal" onClick={this.props.authorise}>Log In</button>
					        </div>
					    </div>
				      </div>
				      <div className="modal-footer cursor-pointer d-flex justify-content-center p-4" data-dismiss="modal" data-toggle="modal" data-target="#signup">
				        <div className="text-light">Don't have an account yet? <span className="text-blue fonr-weight-bold">Sign Up</span></div>
				      </div>
				    </div>
				  </div>
				</div>
		);
	}
}



