import React, { Component } from 'react';

export default class ArtWork extends Component {
  constructor() {
    super();
    this.state = {    
      liked : false,
      likes : 14,
      price : null,
    }

    this.handleLike = this.handleLike.bind(this);
    this.getprice = this.getprice.bind(this);
  }
  componentDidMount() {
    this.getprice();
  }
  handleLike() {
    this.state.liked ?
      this.setState({ liked : false, likes : this.state.likes-1 }) :
      this.setState({ liked : true, likes : this.state.likes+1 })
  }
  getprice() {
    if( Math.random() < 0.2) {
      this.setState({ price : null })
    }
    else if( this.props.price < 1000 ) {
      this.setState({ price : this.props.price + 'K' })
    }
    else if( 1000 <= this.props.price && this.props.price < 100000 ) {
      this.setState({ price : Math.round(this.props.price/1000) + 'L' })
    }
    else if ( 100000 <= this.props.price ) {
      this.setState({ price : Math.round(this.props.price/100000) + 'Cr' })
    }
  }
	render() {
		return(
          <div className="col-6 col-sm-4 col-md-3 pb-5 px-3">
              <div className="painting-card">
                <div className="image">
                  <img className="paint-img" src={this.props.image} width="100%" />
                  <div className="additional-info">
                    <div className="overlay" data-toggle="modal" data-target="#artSelected" onClick={() => this.props.selectModalContent(this.props.index)}></div>
                    {/*<div className={`likes text-white d-none d-md-flex flex-column align-items-center justify-content-center pt-3`} onClick={this.handleLike}>
                      <i className={`ion ion-md-heart font-size-xlarge ${this.state.liked ? 'text-red text-stroke' : ''}`}></i>
                      <div className="font-weight-bold">102 likes</div>
                    </div>*/}
                  </div>
                </div>
                
                <div className="d-flex justify-content-between text-black">
                  <p className="mt-2 mb-1 pt-1 mx-1 truncate" style={{ flex: '1'}}>Blue World is here for now</p>
                  {
                    this.state.price ?
                    <p className="mt-2 mb-1 pt-1 mx-1">₹ {this.state.price}</p> :
                    <p className="mt-2 mb-1 pt-1 mx-1 text-red" style={{ fontWeight : '500' }}>SOLD</p>
                  }
                </div>

                {
                  this.props.profile ?
                  <div className="d-flex justify-content-between text-black mb-1 pb-2 mx-1">
                    <div className="d-flex align-items-center" >
                      <div className={`text-light`}>{ this.props.likeCount } likes</div>
                    </div>
                  </div> :
                  <div className="d-flex justify-content-between text-black mb-1 pb-2 mx-1">
                    <div className="d-flex align-items-center" onClick={this.handleLike} >
                      <i className={`ion transition font-size-xlarge mr-2 ${this.state.liked ? 'ion-md-heart text-red' : 'ion-md-heart-empty'}`} />
                      <div className={`text-light`}>{this.state.liked ? (this.props.likeCount+1) : this.props.likeCount} likes</div>
                    </div>
                  </div>
                }
                
                <div className="d-flex justify-content-between text-black mb-1 pb-2 mx-1">
                  <div className="d-flex align-items-center">
                    {/*<i className={`ion transition font-size-xlarge mr-2 ion-md-share`} onClick={this.handleLike} />*/}
                    <div className={`text-dark text-blue`}>Share</div>
                  </div>
                </div>
              </div>
              {
                this.props.profile ?
                null :
                <div className="d-flex py-3 align-items-center justify-content-between font-size-small cursor-pointer">
                  <div className="image d-flex">
                    <img className="rounded-circle mr-2" src={require('../assets/images/artist.jpg')} width={14} height={14} />
                    <div className="text-black">Username</div>
                  </div>
                  {/*<div className="font-weight-bold text-blue">+ Follow</div>*/}
                  {/*<button className="btn px-2 py-1 bg-blue text-white">+ Follow</button>*/}
                </div> 
              }
              
          </div>
		);
	}
}


