import React, { Component } from 'react';

import Navbar from '../components/Navbar.js';
import ArtModal from '../components/ArtModal.js';
import ArtWork from '../components/ArtWork.js';
import PaintingFilter from '../components/PaintingFilter.js';
import Loading from '../components/Loading.js';

export default class OthersProfilePage extends Component {
	constructor() {
		super();
		this.state = {
			load: false,
			selectedTab: 1,
			selectModalContent : 0,
			following : false,
			alldata : [
				[require('../assets/images/image1.jpg')],
				[require('../assets/images/image2.png')],
				[require('../assets/images/image3.png')],
				[require('../assets/images/image4.png')],
				[require('../assets/images/image5.jpg')],
				[require('../assets/images/image6.jpg')],
				[require('../assets/images/image7.jpg')],
				[require('../assets/images/image8.jpg')],
				[require('../assets/images/image9.jpg')],
				[require('../assets/images/image10.jpg')],
				[require('../assets/images/image11.jpg')],
				[require('../assets/images/image12.jpg')],
				[require('../assets/images/image13.jpg')],
				[require('../assets/images/image14.jpg')],
				[require('../assets/images/image15.jpg')],
				[require('../assets/images/image16.jpg')],
				[require('../assets/images/image17.jpg')],
				[require('../assets/images/image18.jpg')],
				[require('../assets/images/image19.jpg')],
				[require('../assets/images/image20.jpg')],
				[require('../assets/images/image21.jpg')],
				[require('../assets/images/image22.jpg')],
				[require('../assets/images/image23.jpeg')],
				[require('../assets/images/image24.jpg')],
				[require('../assets/images/image25.jpg')],
				[require('../assets/images/image26.jpg')],
				[require('../assets/images/image27.png')],
				[require('../assets/images/image28.jpg')],
				[require('../assets/images/image29.png')],
				[require('../assets/images/image30.jpg')],
				[require('../assets/images/image31.png')],
				[require('../assets/images/image32.jpg')],
				[require('../assets/images/image33.jpg')],
				[require('../assets/images/image34.jpg')],
				[require('../assets/images/image35.jpg')],
				[require('../assets/images/image36.png')],
				[require('../assets/images/image37.jpg')],
				[require('../assets/images/image38.jpg')],
				[require('../assets/images/image39.jpg')],
				[require('../assets/images/image40.jpg')],
				[require('../assets/images/image41.jpg')],
				[require('../assets/images/image42.jpg')],
				[require('../assets/images/image43.jpg')],
				[require('../assets/images/image44.jpg')],
				[require('../assets/images/image45.jpg')],
				[require('../assets/images/image46.jpg')],
			]
		}

		this.selectModalContent = this.selectModalContent.bind(this);
		this.switchTab = this.switchTab.bind(this);
	}
	switchTab(i) {
	    this.setState({ load: true, selectedTab : i });
	    setTimeout(() => this.setState({ load: false }), 2000);
	}
	selectModalContent(i) {
	    this.setState({ selectModalContent : i });
	}

	render() {
		return(
			<div className="main-body mt-5 pt-5">
		        <ArtModal image={this.state.alldata[this.state.selectModalContent]} likeCount={Math.round(Math.random()*500)} />

		          <div className="container custom-container pt-5 mt-4">
		            <div className="row">
		            	<div className="col-12 d-flex flex-column align-items-center borderbottom-light pb-4">
		            		<img className="rounded-circle mb-3" height={70} width={70} src={require('../assets/images/artist.jpg')} /> 
		            		<p className="text-center mb-2 mr-3 font-secondary" style={{ fontSize : '2.5rem'}}>Raghav Garg</p>
		            		<p className="text-center mb-4" style={{ maxWidth : '800px'}}>When editing, keep it simple, and as close to the original as possible. The image should not be smaller. The imag age should not be smaller. The imag age should not be smaller. The image should not be smaller.</p>
		            		<div className="d-flex mb-4">
		            			<div className="px-4"><b>862</b> posts</div>
		            			<div className="px-4"><b>479k</b> followers</div>
		            			<div className="px-4"><b>50k</b> following</div>
		            		</div>
		            		{
		            			this.state.following ?
		            			<button
			            			className="btn border-dark bg-light text-dark font-weight-bold mb-4 px-5 py-2"
			            			onClick={() => { this.setState({ following : false }) }}
			            			>
		            				Following
		            			</button> :
		            			<button
			            			className="btn border-dark bg-dark text-white font-weight-bold mb-4 px-5 py-2"
			            			onClick={() => { this.setState({ following : true }) }}
			            			>
		            				Follow
		            			</button>
		            		}
		            	</div>
		            	<div className="col-12 offset-lg-1 col-lg-10 custom pl-lg-5">
							<Loading load={this.state.load} />
							<div className={`row pt-5 ${this.state.load ? 'd-none' : ''}`}>	
								{	
				            		this.state.alldata.map( (artwork, index) =>
				            			{ return (<ArtWork key={index} index={index} image={artwork[0]} selectModalContent={this.selectModalContent} likeCount={Math.round(Math.random()*500)} price={Math.round(Math.random()*100000)} price={null} profile={true} />) } 
				            		)
				            	}
							</div>
						</div>
		            </div>
		          </div>
		    </div>
		);
	}
}


