import React, { Component } from 'react';

export default class Loading extends Component {
	render() {
		return(
			<div className={`loading ${this.props.load ? 'true' : ''}`}>
				<div className="dot mx-2"></div>
				<div className="dot mx-2"></div>
				<div className="dot mx-2"></div>
			</div>
		);
	}
}


