import React, { Component } from 'react';

export default class AddressModal extends Component {
	constructor() {
		super();
		this.state = {
			step : 1,
		}
	}
	render() {
		return(
				<div className="modal fade" id="adress" tabindex="-1" role="dialog" aria-labelledby="address" aria-hidden="true">
				  <div className="modal-dialog" role="document">
				    <div className="modal-content bg-light border-dark shadow-dark">
				      <div className="modal-header font-secondary p-4">
				        <h5 className="modal-title" id="address">Add Adress</h5>
				        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
				          <span aria-hidden="true">&times;</span>
				        </button>
				      </div>
				      <div className="modal-body bg-white">
				      	<div className="p-5">
				      		{
				      			this.state.step == 1 ?
				      			<div className="w-100">
						        	<input className="form-control w-100 mb-3 pl-3" placeholder="Resident's Full Name" style={{ height : '40px'}} />
						        	<input className="form-control w-100 mb-3 pl-3" placeholder="Mobile Number" style={{ height : '40px'}} />
						        	<textarea className="form-control w-100 mb-3 p-3" placeholder="Full Adress" style={{ height : '140px'}} />
						        	<div className="d-flex">
						        		<input className="form-control w-100 mb-3 mr-4 pl-3" placeholder="State" style={{ height : '40px'}} />
						        		<input className="form-control w-100 mb-3 mr-4 pl-3" placeholder="Pincode" style={{ height : '40px'}} />
						        		<input className="form-control w-100 mb-3 pl-3" placeholder="Country" style={{ height : '40px'}} />
						        	</div>
						        	<button className="btn bg-dark text-white font-weight-bold py-4 w-100 mt-4" onClick={() => this.setState({ step : 2 })}>Sent Otp to verify Mobile</button>
						        </div> :
						        <div className="w-100">
						        	<div className="mb-5 text-black font-weight-bold text-uppercase d-flex align-items-center cursor-pointer" onClick={() => this.setState({ step : 1 })}>
						        		<i className="ion ion-ios-arrow-back mr-3 font-size-xlarge" />
						        		<div>Back</div>
						        	</div>
						        	<p className="text-light">An OTP Has Been Sent to +91-9971696497.<br/>
									Enter the OTP number to verify your Mobile Number.</p>
									<p className="text-light">Didn’t Recieve the message? <span className="text-blue font-weight-bold cursor-pointer">Resend</span> </p>
						        	<input className="form-control w-100 mb-3 pl-3" placeholder="Enter OTP" style={{ height : '40px'}} />
						        	<button className="btn bg-dark text-white font-weight-bold py-4 w-100 mt-4" data-dismiss="modal">Verify Mobile</button>
						        </div>
				      		}
					    </div>
				      </div>
				    </div>
				  </div>
				</div>
		);
	}
}



